@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 21px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  font-feature-settings: "pnum" on, "lnum" on; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  background-color: rgba(0, 0, 0, 0.4);
  color: #ffffff;
  position: fixed;
  width: 100%;
  z-index: 10; }

.page-header__logo-link {
  text-transform: uppercase;
  padding-right: 10px; }

.page-header__title {
  font-weight: 700;
  font-size: 14px;
  line-height: 16px; }

.page-header__title-info {
  font-size: 13px;
  line-height: 15px; }

.page-header__wrapper {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 14px 1.25% 7px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; }

.page-header__phone-number {
  color: #ffffff;
  font-weight: 700;
  font-size: 15px;
  line-height: 18px; }

.page-header__menu-wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 820px;
  width: 100%;
  flex-wrap: wrap; }

.page-header__call-block {
  display: flex;
  max-width: 256px;
  width: 100%;
  justify-content: space-between;
  align-items: center; }

.page-header__call-order {
  color: #ffffff;
  text-decoration: underline;
  line-height: 19px;
  font-weight: 700;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25); }

.page-header__call-order:hover,
.page-header__phone-number:hover {
  color: #8c0014; }

.page-header__call-order:active,
.page-header__phone-number:active {
  opacity: 0.7; }

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 471px;
  padding-right: 10px; }

.main-nav__link {
  color: #ffffff;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25); }

.main-nav__link:hover {
  color: #8c0014; }

.main-nav__link:active {
  opacity: 0.7; }

.promotion__title {
  margin: 0;
  text-transform: uppercase; }

.promotion {
  color: #ffffff;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.45);
  background-repeat: no-repeat;
  background-image: url("../img/backgrounds/promotion-bg@1x.jpg");
  background-size: cover;
  background-position: center 0;
  margin-bottom: 44px; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .promotion {
      background-image: url("../img/backgrounds/promotion-bg@2x.jpg"); } }

.promotion__wrapper {
  padding: 183px 0 111px; }

.promotion__title-first-part {
  font-size: 18px;
  letter-spacing: 0.05em;
  margin-bottom: 17px; }

.promotion__title-second-part {
  font-size: 48px;
  line-height: 56px;
  letter-spacing: 0.08em;
  margin-bottom: 17px; }

.promotion__title {
  display: flex;
  flex-direction: column;
  font-weight: 700; }

.promotion__button {
  display: inline-block;
  vertical-align: top;
  width: 165px;
  color: #ffffff;
  border: 2px solid #ffffff;
  padding: 13px 0 11px;
  background-color: transparent;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  border-radius: 2px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  outline: none; }

.promotion__button:hover {
  color: #8c0014;
  border-color: #8c0014; }

.promotion__button:active {
  opacity: 0.7; }

.promotion__text {
  margin: 0;
  letter-spacing: 0.05em; }

.promotion__text:nth-of-type(2) {
  margin-bottom: 42px; }

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  max-width: 749px;
  width: auto; }

.advantages__title {
  font-weight: 700;
  font-size: 22px;
  line-height: 26px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 26px; }

.advantages__guarantee-title {
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 0.05em;
  margin: 0; }

.advantages__text {
  letter-spacing: 0.05em;
  margin: 0; }

.advantages__guarantee-icon {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 1px solid #e2dede;
  background-color: transparent;
  flex-shrink: 0;
  margin-right: 22px; }

.advantages__item {
  display: flex; }

.advantages__item:not(:last-child) {
  margin-bottom: 20px; }

.advantages__form {
  max-width: 376px;
  width: 100%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  padding: 42px 35px 47px 32px;
  background-color: #ffffff;
  margin-top: -150px; }

.advantages__form-title,
.advantages__form-slogan {
  letter-spacing: 0.05em;
  text-align: center; }

.advantages__form-title {
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  margin: 0; }

.advantages__form-slogan {
  font-size: 16px;
  line-height: 21px;
  margin-bottom: 29px; }

.advantages__wrapper {
  display: flex; }

.advantages__container {
  max-width: 1130px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px; }

.form-call-order__item {
  margin: 0;
  margin-bottom: 26px;
  padding: 0;
  border: 0;
  text-align: center; }

.form-call-order__input {
  border: 1px solid #e2dede;
  background-color: #f8f6f6;
  border-radius: 2px;
  width: 275px;
  padding: 12px 14px 11px;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  color: #000000; }

.form-call-order__input::placeholder,
.form-call-order textarea::placeholder {
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  color: #000000; }

.form-call-order__button {
  display: inline-block;
  vertical-align: top;
  border: none;
  padding: 13px 0 11px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  font-size: 13px;
  line-height: 15px;
  font-weight: 700;
  letter-spacing: 0.05em;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  width: 275px;
  background-color: #8c0014;
  margin: 0 auto; }

.form-call-order {
  text-align: center; }

.form-call-order textarea {
  border-radius: 2px;
  border: 1px solid #e2dede;
  background-color: #f8f6f6;
  border-radius: 2px;
  width: 275px;
  padding: 12px 13px;
  resize: none; }

.price-list {
  background-color: #f8f6f6;
  padding: 44px 0 38px; }

.price-list__title {
  margin-top: 0;
  margin-bottom: 29px;
  font-weight: 700;
  font-size: 22px;
  line-height: 26px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center; }

.price-list th {
  background-color: #8c0014;
  color: #ffffff;
  letter-spacing: 0.05em;
  font-weight: 700;
  text-align: left;
  padding: 20px 0 17px 33px; }

.price-list__breakage-type {
  width: 71.55%; }

.price-list__ammount {
  width: 28.45%; }

.price-list__wrapper {
  max-width: 970px;
  width: 100%;
  margin: 0 auto; }

.price-list__table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 33px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  border-spacing: 0; }

.price-list__button {
  display: inline-block;
  vertical-align: top;
  border: 0;
  background-color: #8c0014;
  color: #ffffff;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25);
  text-transform: uppercase;
  border-radius: 2px;
  font-weight: 700;
  font-size: 13px;
  line-height: 15px;
  text-align: center;
  letter-spacing: 0.05em;
  padding: 13px 0 11px;
  width: 275px;
  cursor: pointer;
  margin-left: 50%;
  transform: translateX(-50%); }

.price-list__work-type {
  letter-spacing: 0.05em;
  padding: 11px 33px 11px;
  border-right: 1px solid #eae7e7; }

.price-list__price {
  font-weight: 700;
  letter-spacing: 0.05em;
  padding: 11px 33px 11px; }

.price-list__table tr:nth-child(2n) {
